<script>
  // @ is an alias to /src
  import { ImagePreview } from 'vant'
  import myProgress from '../../components/Progress'
  import defaultPage from '../../components/DafultPage'

  export default {
    name: 'homePage',
    components: {
      myProgress,
      defaultPage
    },
    data() {
      return {
        active: 0,
        showPopup: false,
        list: ['屏蔽此动态', '屏蔽此人', '举报', '取消'],
        tabs: {
          tabName: ['应援', '商品']
        },
        rankAndHots: {},
        idolInfo: {
          coverurl: '',
          headburl: '',
          headsurl: '',
          id: 1,
          nationality: 1,
          signing: 1,
          singername: ''
        },
        presellList: {},
        supportList: {},
        showAd: true, //是否显示广告
        picLargeArr: [], //大图的数组
        largeIndex: Number, //大图当前滑块的索
        hots: [],
        recommend: []
      }
    },
    created() {
      document.title = '爱豆主页';
    },
    mounted() {
      this.getIdol();
      this.getIdolPresellList();
      this.getIdolSupprotList();
      this.getRangeAndHots();
    },
    methods: {
      getIdol: function () {
        let params = this.$queryParams(location.href);
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE1 + '/user_server/singer/getSingerById', // 爱豆主页
          data: this.$qs.stringify({
            id: params.id
          })
        }).then((res) => {
            if (res.data.status !== 1000) {
              this.$toast(res.data.msg);
              return false;
            } else if (res.data.data !== null) {
              this.idolInfo = res.data.data;
            }
          }
        ).catch((err) => {
          console.log(err);
        })
      },
      getIdolPresellList: function () {
        let params = this.$queryParams(location.href);
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE4 + '/GoodsAPI/presaleListBySingerId', // 爱豆预售列表
          data: this.$qs.stringify({
            singerid: params.id,
            page: 0
          })
        }).then((res) => {
            if (res.data.status !== 1000) {
              this.$toast(res.data.msg);
              return false;
            } else if (res.data.data !== null) {
              this.presellList = res.data.data;
            }
          }
        ).catch((err) => {
          console.log(err);
        })
      },
      getRangeAndHots: function () {
        let params = this.$queryParams(location.href);
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE5 + '/popularList/getYearPopularBySingerId', // 爱豆预售列表
          data: this.$qs.stringify({
            id: params.id,
          })
        }).then((res) => {
            if (res.data.status !== 1000) {
              return false;
            } else if (res.data.data !== null) {
              this.rankAndHots = res.data.data;
            }
          }
        ).catch((err) => {
          console.log(err);
        })
      },
      getIdolSupprotList: function () {
        let params = this.$queryParams(location.href);
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE4 + '/assistance/getActivityBySingerIdPage', // 爱豆动态
          data: this.$qs.stringify({
            singerid: params.id,
            pageNum: 0,
            pageSize: 5
          })
        }).then((res) => {
            if (res.data.status !== 1000) {
              this.$toast(res.data.msg);
              return false;
            } else if (res.data.data !== null) {
              this.supportList = res.data.data;
              for (let i = 0; i < this.supportList.list.length; i ++) {
                if (this.supportList.list[i].moneyneed === 0) {
                  this.supportList.list[i].percent = this.supportList.list[i].integralnow / this.supportList.list[i].integralneed * 100;
                }
                if (this.supportList.list[i].integralneed === 0) {
                  this.supportList.list[i].percent = (this.supportList.list[i].moneynow * 100) / (this.supportList.list[i].moneyneed * 100) / 100;
                }
                if (this.supportList.list[i].endtime === 0) {
                  this.supportList.list[i].endtime = '长期';
                }
              }
            }
          }
        ).catch((err) => {
          console.log(err);
        })
      },
      //是否显示广告
      isShowAd: function () {
        this.$toast('关闭成功');
        this.showAd = false;
      },
      changeActive: function () {
        // alert(this.active);
      },
      //查看大图
      viewPicture: function (i, d) {
        this.picLargeArr = d;
        ImagePreview({
          images: d,
          startPosition: i
        })
      },
      tipOffs: function (n) {
        this.showPopup = ! this.showPopup;
        if ((typeof n).toLowerCase() === 'string') {
          this.$toast(n);
        }
      }
    }
  }
</script>
